import React from 'react'
import styled from 'styled-components'

export default function ReviewCard({img, name, description}) {
    return (
        <Container>
            <Description>{description}</Description>
            <Wrapper>
                <Avatar src={img} alt=""/>
                <Name>{name}</Name>
            </Wrapper>
        </Container>
    )
}

const Container = styled.div`
    height: 400px;
    border: 1px solid grey;
    width: 300px;
    background-color: #000000c5;
    border-radius: 5px;
    padding: 20px;
    position: relative;
    box-shadow: 0 5px 10px #000000a4;
    animation: floating 5s infinite alternate linear;
    @keyframes floating {
        to {
            transform: translateY(-30px);
        }
    }
    &:nth-child(1) {
        align-self: flex-end;
        animation-delay: 2s;
    }
    &:nth-child(2) {
        align-self: flex-end;
    }
    &:nth-child(3) {
        align-self: center;
        animation-delay: 1s;
    }
    @media screen and (max-width: 900px) {
        animation: none;
        &:nth-child(1) {
            margin-bottom: -50px;
        }   
        &:nth-child(2) {
           margin-top: -20px;
           margin-right: -10vw;
        }
    }
`
const Description = styled.p`
    font-size: 0.9rem;
    color: white;
    position: relative;
    text-align: justify;
    &::after {
        content: '';
        position: absolute;
        bottom: -30px;
        right: -50px;
        width: 80%;
        height: 1px;
        background-color: #00000053;
    }
`
const Wrapper = styled.div`
    display: flex;
    gap: 30px;
    align-items: center;
    position: absolute;
    bottom: 30px;
    left: -50px;
`
const Avatar = styled.img`
    width: 100px;
    box-shadow: 0 2px 10px #00000067;
`
const Name = styled.p`
    color: white;
    font-family: 'Big Shoulders Display', cursive;
    font-size: 2rem;
`

