import React from 'react';
import { SmoothProvider } from 'react-smooth-scrolling'
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom'
import Navbar from './components/Navbar';
import Homepage from './components/pages/Homepage';
import Footer from './components/Footer';
import Aboutpage from './components/pages/Aboutpage';
import Contactpage from './components/pages/Contactpage';
import Productspage from './components/pages/Productspage';
import Cartpage from './components/pages/Cartpage';
import Accountpage from './components/pages/Accountpage';
import Loginpage from './components/pages/Loginpage';
import { useSelector } from 'react-redux';
import { login, selectAdmin, selectUser } from './features/userSlice';
import { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { auth } from './components/firebase';
import Adminpage from './components/pages/Adminpage';
import { useState } from 'react';
import PopupMessage from './components/PopupMessage'
import { selectErrorMessage, selectShowError } from './features/errorSlice';


function App() {
  const user = useSelector(selectUser)
  const admin = useSelector(selectAdmin)
  const dispatch = useDispatch()
  const [owner, setOwner] = useState(false)
  const showErrorIsOpen = useSelector(selectShowError)
  const error = useSelector(selectErrorMessage)

  useEffect(() => {
    auth.onAuthStateChanged(user => {
      if (user) {
        dispatch(login({
          displayName: user.displayName,
          email: user.email,
          photoUrl: user.photoURL
        }))
        if (user.email === admin) {
          setOwner(true)
        }
      }
    })
  }, [dispatch])


  return (
    <div>
      <Router basename="/cutite-demo">
        <Navbar />
        <SmoothProvider skew={false} ease={0.1} style={{ height: 0 }}>
          < Switch >
            <Route path="/" exact component={Homepage} />
            <Route path="/despre" exact component={Aboutpage} />
            <Route path="/contact" exact component={Contactpage} />
            <Route path="/produse" exact component={Productspage} />
            <Route path="/cos" exact component={user ? Cartpage : Loginpage} />
            <Route path="/cont" exact component={owner && user ? Adminpage : user ? Accountpage : Loginpage} />
          </Switch>
          <Footer />
        </SmoothProvider >

        {showErrorIsOpen && <PopupMessage error={error} />}
      </Router >
    </div>
  );
}

export default App;
