import { Button, TextField } from '@material-ui/core'
import { Grid } from '@material-ui/core'
import React from 'react'
import styled from 'styled-components'
import bg from '../../images/bg5.png'
import AddIcon from '@material-ui/icons/Add';
import AddPhotoAlternateIcon from '@material-ui/icons/AddPhotoAlternate';
import { InputLabel } from '@material-ui/core'
import { FormControl } from '@material-ui/core'
import { Select } from '@material-ui/core'
import { MenuItem } from '@material-ui/core'
import { useState } from 'react'
import { Fab } from '@material-ui/core'
import {Container as Contain} from '@material-ui/core';
import { useRef } from 'react'
import { db } from '../firebase'
import { useEffect } from 'react'
import { useDispatch } from 'react-redux'
import { selectError, showErrorIsTrue } from '../../features/errorSlice'
import useStorage from '../useStorage'
import Progressbar from '../Progressbar'
import firebase from 'firebase';

export default function Adminpage() {
    const [color, setColor] = useState('')
    const [size, setSize] = useState('')
    const [material, setMaterial] = useState('')
    const [handler, setHandler] = useState('')
    const [picture, setPicture] = useState(null)
    const [name, setName] = useState('')
    const [id, setId] = useState('')
    const [price, setPrice] = useState('')
    const [stock, setStock] = useState('')
    const [description, setDescription] = useState('')
    const dispatch = useDispatch()


    const handleColor = (e) => {
        setColor(e.target.value)
    }
    const handleSize = (e) => {
        setSize(e.target.value)
    }
    const handleMaterial = (e) => {
        setMaterial(e.target.value)
    }
    const handleHandler = (e) => {
        setHandler(e.target.value)
    }

    const { picUrl } = useStorage(picture);

    useEffect(() => {
        if (picUrl) {
            setPicture(null);
        }
        return;
    }, [picUrl, setPicture])

    const types = [
        "image/apng",
        "image/bmp",
        "image/gif",
        "image/jpeg",
        "image/jpg",
        "image/pjpeg",
        "image/png",
        "image/tiff",
        "image/webp"
    ];

    const handlePicture = e => {
        let img = e.target.files[0];

        if (img && types.includes(img.type)) {
            setPicture(img);
            dispatch(selectError(`Ai selectat imaginea ${img.name} ✅`));
            dispatch(showErrorIsTrue())
        } else  {
            setPicture(null);
            dispatch(selectError('Imaginea selectată nu este validă ❌'));
            dispatch(showErrorIsTrue())
        }
    }

    const handleSubmit = (e) => {
        e.preventDefault()

        db.collection('knives').add({
            name: name,
            id: id,
            image: picUrl || '',
            price: price,
            stock: stock,
            option1: color,
            option2: size,
            option3: material,
            option4: handler,
            description: description,
            timestamp: firebase.firestore.FieldValue.serverTimestamp()
        })

        // e.target.reset()

        setColor('')
        setSize('')
        setMaterial('')
        setHandler('')
        setPicture(null)
        setName('')
        setId('')
        setPrice('')
        setStock('')
        setDescription('')
       
    }

    return (
        <Container >
            <Title>Acasă ⋙ Administrator</Title>
                <FormWrapper  onSubmit={handleSubmit}>
                    {picture && <Progressbar picture={picture} setPicture={setPicture}/>}

                    <TextField 
                        required 
                        label="Nume Produs" 
                        fullWidth
                        value={name}
                        onChange={e => setName(e.target.value)}
                    />
                    
                    <Grid container spacing={1}>
                        <Grid item xs={10} sm={11}>
                            <TextField 
                            label="ID" 
                            fullWidth
                            value={id}
                            onChange={e => setId(e.target.value)}
                        />
                        </Grid>
                        <Grid item xs={2} sm={1}>
                            <Contain maxWidth="xs" title="Adaugă imagine">
                                <Fab size="small" color="primary" aria-label="add" 
                                    style={{width: 40, height: 40, padding: 0, marginLeft: -20, marginBottom: -60}} >
                                    <Label>
                                        <InputFile type="file" onChange={handlePicture}/>
                                        <AddPhotoAlternateIcon />
                                    </Label>
                                </Fab>
                            </Contain>
                        </Grid>
                    </Grid>

                    <Grid container spacing={1}>
                        <Grid item xs={12} sm={6}>
                            <TextField
                                label="Preț"
                                type="number"
                                fullWidth
                                required
                                value={price}
                                onChange={e => setPrice(e.target.value)}
                            />
                        </Grid>
                        <Grid item xs={12} sm={6}>
                            <TextField
                                label="Stoc"
                                type="number"
                                fullWidth
                                value={stock}
                                onChange={e => setStock(e.target.value)}
                            />
                        </Grid>
                    </Grid>

                    <Grid container spacing={1}>
                        <Grid item xs={12} sm={6}>
                            <FormControl fullWidth >
                                <InputLabel id="culoare">Culoare</InputLabel>
                                <Select
                                    labelId="culoare"
                                    value={color}
                                    onChange={handleColor}
                                >
                                    <MenuItem value="">
                                        <em>None</em>
                                    </MenuItem>
                                    <MenuItem value="rosu">Roșu</MenuItem>
                                    <MenuItem value="negru">Negru</MenuItem>
                                    <MenuItem value="alb">Alb</MenuItem>
                                </Select>
                            </FormControl>
                        </Grid>
                        <Grid item xs={12} sm={6}>
                            <FormControl fullWidth>
                                <InputLabel id="size">Mărime</InputLabel>
                                <Select
                                    labelId="size"
                                    value={size}
                                    onChange={handleSize}
                                >
                                    <MenuItem value="">
                                        <em>None</em>
                                    </MenuItem>
                                    <MenuItem value="mic">Mic</MenuItem>
                                    <MenuItem value="medius">Mediu</MenuItem>
                                    <MenuItem value="mare">Mare</MenuItem>
                                </Select>
                            </FormControl>
                        </Grid>
                    </Grid>

                    <Grid container spacing={1}>
                        <Grid item xs={12} sm={6}>
                            <FormControl fullWidth>
                                <InputLabel id="material">Material</InputLabel>
                                <Select
                                    labelId="material"
                                    value={material}
                                    onChange={handleMaterial}
                                >
                                    <MenuItem value="">
                                        <em>None</em>
                                    </MenuItem>
                                    <MenuItem value="titaniu">Titaniu</MenuItem>
                                    <MenuItem value="carbon">Fibră de carbon</MenuItem>
                                    <MenuItem value="damasc">Damasc</MenuItem>
                                </Select>
                            </FormControl>
                        </Grid>
                        <Grid item xs={12} sm={6}>
                            <FormControl fullWidth>
                                <InputLabel id="handler">Lemn mâner</InputLabel>
                                <Select
                                    labelId="handler"
                                    value={handler}
                                    onChange={handleHandler}
                                >
                                    <MenuItem value="">
                                        <em>None</em>
                                    </MenuItem>
                                    <MenuItem value="nuc">Nuc</MenuItem>
                                    <MenuItem value="maslin">Măslin</MenuItem>
                                    <MenuItem value="brad">Brad</MenuItem>
                                    <MenuItem value="cires">Cireș</MenuItem>
                                    <MenuItem value="fag">Fag</MenuItem>
                                </Select>
                            </FormControl>
                        </Grid>
                    </Grid>

                    <TextField
                        label="Descriere"
                        value={description}
                        onChange={e => setDescription(e.target.value)}
                        multiline
                        rows={6}
                        margin="dense"
                    />

                    <Button
                        type="submit"
                        variant="contained"
                        color="default"
                        endIcon={<AddIcon />}
                    >
                        Adaugă produs
                    </Button>
                </FormWrapper>
        </Container>
    )
}




const Container = styled.article`
    min-height: 100%;
    background: url(${bg});
`
const Title = styled.h1`
    color: #292929;
    margin-left: 20vw;
    padding-top: 10vh;
    font-size: clamp(1rem, 5vw, 2rem);
    font-family: 'Big Shoulders Display', cursive;
`
const FormWrapper = styled.form`
    display: flex;
    flex-direction: column;
    margin: 200px auto 0;
    width: 700px;
    max-width: 90vw;
    flex: 1;
    padding: 20px;
    position:relative;
    button {
        width: 200px;
        height: 40px;
    }
`
const Label = styled.label`
    width: 40px;
    height: 40px;
    border-radius: 50%;
    cursor: pointer;
    >svg {
        margin-top: 7px;
        margin-left: 4px;
    }
`
const InputFile = styled.input`
    height: 0;
    width: 0;
    opacity: 0;
`