import React from 'react'
import { useEffect } from 'react'
import styled from 'styled-components'
import useStorage from './useStorage'


export default function Progressbar({picture, setPicture}) {
    const {picUrl, progress} = useStorage(picture)

    // useEffect(() => {
    //     if (picUrl) {
    //         setPicture(null)
    //     }
    // }, [picUrl, setPicture])

    return (
        <Container style={{width: progress + '%'}}>
            
        </Container>
    )
}




const Container = styled.aside`
    position: absolute;
    top: 0;
    left: 20px;
    height: 5px;
    max-width: calc(100% - 40px);
    background-image: linear-gradient(to left, black, var(--c1), var(--c1t));
`