import React from 'react'
import styled from 'styled-components'
import bg from '../../images/about-bg.jpg'
import cover from '../../images/cover.jpg'
import bg5 from '../../images/bg5.png'
import w1 from '../../images/w1.jpg'
import w2 from '../../images/w2.jpg'
import w3 from '../../images/w3.jpg'
import w4 from '../../images/w4.jpg'
import profileImg from '../../images/profile-img.jpg'
import workImg from '../../images/work-img.jpg'
import WorkerCard from '../WorkerCard'
import Counters from '../Counters'

export default function Aboutpage() {
    return (
        <Container>
            <Title>Acasă ⋙ Despre</Title>
            <Wrapper>
                <ProfileBgText>PROFILE PROFILE PROFILE</ProfileBgText>
                <WorkBgText>WORK WORK WORK</WorkBgText>
                <Profile>
                    <ProfileImg src={profileImg} alt="" />
                    <ProfileDescription>
                        <ProfileTitle>Profile</ProfileTitle>
                        <ProfileText>
                        Lorem ipsum dolor sit amet consectetur adipisicing elit. Facilis omnis ipsam eveniet obcaecati iste, iusto dolorem corporis error illum magnam. Nulla molestiae ad sint quasi cum accusamus, commodi inventore architecto tempora animi unde hic optio aliquid perferendis iste odio at soluta laudantium numquam cupiditate ut impedit, amet eligendi. Perferendis asperiores beatae dignissimos quis iste officia officiis voluptas nisi, pariatur culpa nostrum voluptates ducimus eius non consequatur nam veniam iure voluptatibus sed! Ullam minus ipsa, molestias debitis ea ex, hic dolore nobis accusantium repudiandae placeat corrupti nesciunt deserunt, explicabo animi. Unde laboriosam autem omnis ipsum tenetur soluta corporis molestiae provident repudiandae.
                        </ProfileText>
                    </ProfileDescription>
                </Profile>

                <Work>
                    <TeamImg src={workImg} alt="" />
                    <WorkDescription>
                    Lorem ipsum dolor sit amet consectetur adipisicing elit. Id omnis repellendus est laboriosam exercitationem eaque. Pariatur alias architecto eum facilis dolorum omnis, repudiandae doloremque consequuntur cum deleniti fugiat temporibus neque!
                    <br/>
                    <br/>
                    Lorem ipsum dolor sit amet consectetur adipisicing elit. Id omnis repellendus est laboriosam exercitationem eaque dolorum omnis, repudiandae doloremque consequuntur cum deleniti fugiat temporibus neque!
                    <br/>
                    <br/>
                    Lorem ipsum dolor sit amet consectetur adipisicing elit. Id omnis repellendus est laboriosam exercitationem eaque. Pariatur alias architecto eum facilis dolorum omnis, repudiandae doloremque consequuntur cum deleniti fugiat temporibus neque!
                    Lorem ipsum dolor sit amet consectetur adipisicing elit. Id omnis repellendus est laboriosam exercitationem eaque. Pariatur alias architecto eum facilis dolorum omnis, repudiandae doloremque consequuntur cum deleniti fugiat temporibus neque!
                    <br/>
                    </WorkDescription>
                </Work>

                <Team>
                    <WorkerCard img={w4} title="Manager" name="Mircea"/>
                    <WorkerCard img={w1} title="Operator" name="Constantin"/>
                    <WorkerCard img={w2} title="Designer" name="Marius"/>
                    <WorkerCard img={w3} title="Inginer" name="Sorin"/>
                </Team>

                <Counters />
            </Wrapper>
        </Container>
    )
}




const Container = styled.article`
    min-height: 300vh;
    background: url(${bg}) no-repeat top center /contain;
`
const Title = styled.h1`
    color: whitesmoke;
    margin-left: 20vw;
    padding-top: 10vh;
    font-size: clamp(1rem, 5vw, 2rem);
    font-family: 'Big Shoulders Display', cursive;
    @media screen and (min-width: 900px) {
        padding-top: 30vh;
    }
`
const Wrapper = styled.div`
    /* background: linear-gradient(to bottom, transparent 70%, #00000076 100%), url(${bg5}); */
    background: url(${bg5});
    margin-top: 10vh;
    height: 100%;
    position: relative;
    padding: 5vw;
    overflow: hidden;
`
const ProfileBgText = styled.p`
    position: absolute;
    top: 500px;
    right: -10%;
    font-size: 15rem;
    white-space: nowrap;
    color: lightgray;
`
const WorkBgText = styled.p`
    position: absolute;
    top: 1700px;
    right: -10%;
    font-size: 15rem;
    white-space: nowrap;
    -webkit-text-stroke: 3px lightgray;
    color: transparent;

`
const Profile = styled.div`
    width: 700px;
    max-width: 100%;
    color: lightgray;
    padding: 10px;
    margin:  200px 0 300px;
    background-image: linear-gradient(#0000009e, #0000009e);
    position: relative;
    z-index: 1;
    border-radius: 5px;
    box-shadow: 50px -50px 0 #000000c3;
    &::after {
        position: absolute;
        content: '';
        top: 50%;
        right: 50%;
        width: 200px;
        height: 500px;
        background-color: #00000071;
        z-index: 0;
    }
`
const ProfileImg = styled.img`
    object-fit: contain;
    width: 100%;
    margin-right: -100px;
`
const ProfileDescription = styled.div`
    height: 50%;
`
const ProfileTitle = styled.h2`
    font-size: clamp(2rem, 5vw, 3rem);
    margin: 20px;
    position: relative;
    font-family: 'Big Shoulders Display', cursive;
    &::before {
        content: '';
        position: absolute;
        left: -70px;
        bottom: -10px;
        width: 200px;
        height: 2px;
        background-color: #333333;
    }
`
const ProfileText = styled.p`
    text-align: justify;
    font-size: clamp(0.9rem, 5vw, 1.3rem);
`
const Work = styled.div`
    margin: 100px -5vw 100px auto;
    width: 1000px;
    max-width: 100%;
    background-image: linear-gradient(#4444449e, #4444449e);
    display: flex;
    border-radius: 5px;
    box-shadow: -50px 50px 0 #000000c3;
    position: relative;
    z-index: 1;
    @media screen and (max-width: 900px) {
        flex-direction: column;
    }
`
const TeamImg = styled.img`
    object-fit: contain;
    width: 700px;
    max-width: 70vw;
    margin-top: -100px;
    margin-left: -50px;
`
const WorkDescription = styled.p`
    padding: 1vw;
    color: white;
    font-size: clamp(0.9rem, 5vw, 1.3rem);
    text-align: justify;
`
const Team = styled.div`
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    gap: 50px;
    margin: 200px 0 100px;
    position: relative;
    &::after {
        content: '';
        position: absolute;
        bottom: -50px;
        width: 50%;
        height: 2px;
        left: 50%;
        transform: translateX(-50%);
        background-color: #414141;
    }
    &::before {
        content: '';
        position: absolute;
        top: -50px;
        width: 50%;
        height: 2px;
        left: 50%;
        transform: translateX(-50%);
        background-color: #414141;
    }
`